<template>
  <div id="container">
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane label="管理员添加" name="first">
        <div id="firstBox">
          注册账号：<el-input
            placeholder="输入注册账号"
            v-model="input"
            clearable
          >
          </el-input
          ><br />
          账号密码：<el-input placeholder="输入密码" v-model="input" clearable>
          </el-input
          ><br />
          确认密码：<el-input placeholder="确认密码" v-model="input" clearable>
          </el-input>
          <br />
          <el-button type="primary">确认注册</el-button>
        </div>
      </el-tab-pane>

      <el-tab-pane label="员工账号管理" name="second">
        <el-table
          :data="
            tableData.filter(
              (data) =>
                !search ||
                data.name.toLowerCase().includes(search.toLowerCase())
            )
          "
          :key="tableData.id"
          style="width: 100%"
        >
          <el-table-column label="工作开始日期" prop="date"> </el-table-column>
          <el-table-column label="姓名" prop="name"> </el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-input
                v-model="search"
                size="mini"
                placeholder="输入派送员名字进行搜索"
              />
            </template>
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)"
                >Edit</el-button
              >
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="角色管理" name="third">
        {{ info }}
      </el-tab-pane>
      <el-tab-pane label="定时任务补偿" name="fourth">定时任务补偿</el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [
        {
          id: 1,
          date: "2016-05-03",
          name: "王小虎",
        },
        {
          id: 2,
          date: "2016-05-02",
          name: "王小虎",
        },
        {
          id: 3,
          date: "2004",
          name: "王小虎",
        },
        {
          id: 4,
          date: "2001",
          name: "王小虎",
        },
        {
          id: 5,
          date: "2008",
          name: "王小虎",
        },
        {
          id: 6,
          date: "2016",
          name: "王小虎",
        },
        {
          id: 7,
          date: "1234",
          name: "王小虎",
        },
      ],
      multipleSelection: [],
      activeName: "second",
      input: "",
      search: "",
      info: null,
    };
  },
  methods: {
    handleClick(tab, event) {},
    handleEdit(index, row) {
      console.log(index, row);
    },
    handleDelete(index, row) {
      // splice 的参数，第一个是数组开始的下标
      this.tableData.splice(index, 1);
    },
  },
  mounted() {
    this.$axios({
      method: "post",
      url: "http://8.140.56.185:8085/user/login",
      data: `Account=${"18582707259"}&password=${"987654"}`,
    }).then((o) => {
      const { data } = o;
      if (data.code != 0) return;
      sessionStorage.setItem("token", data.token);
      console.log(sessionStorage.getItem("token"));
    });
  },
};
</script>

<style scoped>
#container {
  background-color: white;
  min-height: 800px;
}
#firstBox {
  width: 500px;
  height: 350px;
  border: rgb(64, 158, 255) 1px solid;
  border-radius: 10px;
  margin: 160px auto;
}
#firstBox .el-input--suffix {
  width: 200px;
  margin: 20px 0;
  padding: 0 20px;
}
</style>